"use client"

import { useRouter } from "next/navigation"
import { useAuth } from "@/context/auth-context"
import { useState } from "react"
import ItemForm from "./item-form"

// Modal zoom foto
function PhotoModal({ src, onClose }: { src: string, onClose: () => void }) {
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/70" onClick={onClose}>
      <img
        src={src}
        alt="Zoom Foto"
        className="max-w-full max-h-[80vh] rounded-xl shadow-2xl border-4 border-white"
        onClick={e => e.stopPropagation()}
      />
    </div>
  )
}

interface ItemDetailViewProps {
  item: any
}

export default function ItemDetailView({ item }: ItemDetailViewProps) {
  const router = useRouter()
  const { updateItem, users } = useAuth()
    // Helper untuk ambil nama user pembuat item
    const getCreatorName = (userId: string) => {
      const creator = users.find((u) => u.id === userId)
      return creator?.name || "Tidak diketahui"
    }
  const [isEditing, setIsEditing] = useState(false)

  const handleUpdate = (id: string, data: any) => {
    updateItem(id, data)
    setIsEditing(false)
  }

  const [zoomPhoto, setZoomPhoto] = useState<string | null>(null)

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <button
          onClick={() => router.back()}
          className="text-primary hover:text-primary/80 transition-colors font-semibold flex items-center gap-2"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
          Kembali
        </button>
        <button
          onClick={() => setIsEditing(!isEditing)}
          className="bg-primary text-primary-foreground px-6 py-2 rounded-lg hover:bg-primary/90 transition-colors font-semibold"
        >
          {isEditing ? "Batal Edit" : "Edit Item"}
        </button>
      </div>

      {isEditing ? (
        <ItemForm editingItem={item} onUpdate={handleUpdate} onClose={() => setIsEditing(false)} />
      ) : (
        <>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="md:col-span-2 bg-white rounded-xl shadow-lg p-8 border border-border space-y-6">
              {/* Detail Alat */}
              <div className="mb-8 pb-6 border-b border-border">
                <h2 className="text-2xl font-bold text-foreground mb-4">Detail Alat</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">ID Barang / Kode Aset</p><p className="text-lg font-semibold text-foreground">{item.assetId || '-'}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Nama Mesin</p><p className="text-lg font-semibold text-foreground">{item.machineName || '-'}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Kategori</p><p className="text-lg font-semibold text-foreground capitalize">{item.category}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Brand / Merk</p><p className="text-lg font-semibold text-foreground">{item.brand || '-'}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Model / Tipe</p><p className="text-lg font-semibold text-foreground">{item.model || '-'}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Nomor Seri</p><p className="text-lg font-semibold text-foreground">{item.serialNumber || '-'}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Nomor Inventaris / Asset Tag</p><p className="text-lg font-semibold text-foreground">{item.assetTag || '-'}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Kuantitas</p><p className="text-lg font-semibold text-foreground">{item.quantity}</p></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Lokasi</p><p className="text-lg font-semibold text-foreground">{item.location}</p></div>
                </div>
                {/* Foto Item */}
                {item.photos && item.photos.length > 0 && (
                  <div className="mt-6">
                    <h3 className="text-lg font-bold text-foreground mb-2">Foto Item</h3>
                    <div className="flex flex-wrap gap-3">
                      {item.photos.map((photo: string, idx: number) => (
                        <img
                          key={idx}
                          src={photo}
                          alt={`Foto ${idx + 1}`}
                          className="w-32 h-32 object-cover rounded-lg border border-border cursor-zoom-in hover:scale-105 transition-transform"
                          onClick={() => setZoomPhoto(photo)}
                        />
                      ))}
                    </div>
                    {zoomPhoto && <PhotoModal src={zoomPhoto} onClose={() => setZoomPhoto(null)} />}
                  </div>
                )}
                <div className="mt-4">
                  <p className="text-muted-foreground text-sm mb-1">Deskripsi</p>
                  <p className="text-base text-foreground whitespace-pre-line min-h-6">{item.description || <span className="italic text-muted-foreground">Tidak ada deskripsi</span>}</p>
                </div>
              </div>

              {/* Detail Kondisi */}
              <div className="mb-8 pb-6 border-b border-border">
                <h2 className="text-2xl font-bold text-foreground mb-4">Detail Kondisi</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Status Mesin</p><span className={`inline-block px-3 py-1 rounded-full text-sm font-semibold ${item.statusMesin === "Normal" ? "bg-green-100 text-green-700" : item.statusMesin === "Maintenance" ? "bg-yellow-100 text-yellow-700" : item.statusMesin === "Rusak" ? "bg-red-100 text-red-700" : item.statusMesin === "Standby" ? "bg-blue-100 text-blue-700" : "bg-gray-100 text-gray-700"}`}>{item.statusMesin || "-"}</span></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Tingkat Prioritas</p><span className={`inline-block px-3 py-1 rounded-full text-sm font-semibold ${item.tingkatPrioritas === "Low" ? "bg-green-100 text-green-700" : item.tingkatPrioritas === "Medium" ? "bg-yellow-100 text-yellow-700" : item.tingkatPrioritas === "High" ? "bg-orange-100 text-orange-700" : item.tingkatPrioritas === "Critical" ? "bg-red-100 text-red-700" : "bg-gray-100 text-gray-700"}`}>{item.tingkatPrioritas || "-"}</span></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Kondisi Fisik</p><span className={`inline-block px-3 py-1 rounded-full text-sm font-semibold ${item.kondisiFisik === "Bagus" ? "bg-green-100 text-green-700" : item.kondisiFisik === "Sedang" ? "bg-yellow-100 text-yellow-700" : item.kondisiFisik === "Buruk" ? "bg-red-100 text-red-700" : "bg-gray-100 text-gray-700"}`}>{item.kondisiFisik || "-"}</span></div>
                  <div className="bg-muted rounded-lg p-4"><p className="text-muted-foreground text-sm mb-1">Jam Operasional Total</p><p className="text-lg font-semibold text-foreground">{item.jamOperasional || '-'}</p></div>
                </div>
              </div>

              <div className="border-t border-border pt-6">
                <h3 className="font-semibold text-foreground mb-3">Informasi Koordinat</h3>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-muted-foreground mb-1">Latitude</p>
                    <p className="text-foreground font-mono">{item.latitude.toFixed(6)}</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground mb-1">Longitude</p>
                    <p className="text-foreground font-mono">{item.longitude.toFixed(6)}</p>
                  </div>
                </div>
              </div>
            </div>

            <div className="space-y-4">
              <div className="bg-white rounded-xl shadow-lg p-6 border border-border">
                <h3 className="font-semibold text-foreground mb-4">Informasi Penyimpanan</h3>
                <div className="space-y-3 text-sm">
                  <div>
                    <p className="text-muted-foreground">Dibuat oleh</p>
                    <p className="font-semibold text-foreground">{getCreatorName(item.createdBy)}</p>
                  </div>
                  <div className="border-t border-border pt-3">
                    <p className="text-muted-foreground">Tanggal Dibuat</p>
                    <p className="font-semibold text-foreground">
                      {new Date(item.createdAt).toLocaleDateString("id-ID")}
                    </p>
                  </div>
                  <div className="border-t border-border pt-3">
                    <p className="text-muted-foreground">Terakhir Diperbarui</p>
                    <p className="font-semibold text-foreground">
                      {new Date(item.updatedAt).toLocaleDateString("id-ID")}
                    </p>
                  </div>
                </div>
              </div>

              <div className="bg-white rounded-xl shadow-lg p-6 border border-border">
                <h3 className="font-semibold text-foreground mb-4">Peta Lokasi</h3>
                <div className="relative w-full h-64 bg-muted rounded-lg overflow-hidden border border-border">
                  <iframe
                    width="100%"
                    height="100%"
                    style={{ border: "none" }}
                    src={`https://maps.google.com/maps?q=${item.latitude},${item.longitude}&z=15&output=embed`}
                    title="Lokasi Item"
                  />
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  )
}
